import {
	login,
	ajax,
	formatDate
} from '@/static/js/base.js'
import api from '@/static/js/api.js'
import MescrollEmpty from '@/components/mescroll-uni/components/mescroll-empty.vue'
import {
	mapGetters,
	mapMutations
} from 'vuex'
export default {
	components: {
		MescrollEmpty
	},
	data() {
		return {
			bannerArr: [],
			classifyArr: [],
			demandArr: [],
			upOption: {
				textNoMore: '木有更多了', // 没有更多数据的提示文本
				empty: {
					tip: '~ 暂无数据 ~', // 提示
				}
			},
			list: []
		}
	},
	onLoad() {
		var that = this
		// uni.getSetting({
		// 	success(res) {
		// 		if (res.authSetting['scope.userInfo']) {
		// 			uni.getUserInfo({
		// 				success(res) {
		// 					that.setUserInfo(res.userInfo)
		// 					// that.$store.commit('setUserInfo', res.userInfo)
		// 					login()
		// 				}
		// 			})
		// 		}
		// 	}
		// })
		
		// 首页banner请求
		ajax({
			url: api.index_banner
		}).then(res => {
			if (res.error_code == 4000) {
				this.bannerArr = res.data
			}
		})
		// 求购分类推荐入口
		ajax({
			url: api.home_push
		}).then(res => {
			if (res.error_code == 4000) {
				this.classifyArr = res.data
			}
		})
		// 求购分类默认入口
		ajax({
			url: api.home_plain
		}).then(res => {
			if (res.error_code == 4000) {
				this.demandArr = res.data
			}
		})
	},
	computed: {
		...mapGetters(['userInfo']),
	},
	methods: {
		getphonenumber(e) {
			console.log(e)
		},
		...mapMutations(['setUserInfo']),
		getUserInfo(e) {
			if (e.detail.errMsg == 'getUserInfo:ok') {
				this.setUserInfo(e.detail.userInfo)
				login()
			}
		},
		upCallback(mescroll) {
			this.getList(mescroll, curPageData => {
				if (mescroll.num == 1) this.list = []; //如果是第一页需手动制空列表
				this.list = this.list.concat(curPageData)
				this.$nextTick(() => {
					mescroll.endSuccess(curPageData.length);
				})
			})
		},
		getList(mescroll, cb) {
			ajax({
				url: api.home_news,
				data: {
					number: 10,
					page: mescroll.num,
				}
			}).then(res => {
				var list = res.data || []
				list.forEach(item => {
					item.time = formatDate(item.ctime)
				})
				cb(list)
			})
		},
		postWant(){
			uni.navigateTo({
				url:'../postWant/postWant'
			})
		}
	}
}
